@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////


$base-font-family: 'Gotu','Noto Sans JP',sans-serif;
$second-font-family: 'Lato','Noto Serif JP', sans-serif;
// ※検証ツールでしっかり反映されているか確認する

body {
  font-family: $base-font-family;
}

// フォントウェイト
$regular: 400;
$medium: 500;
$bold: 700;
// $regular: 300;
// $normal: 400;
// $bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$color-black: #0D2936;
$color-black2: #000;
$color-white: #fff;
$color-green: #408F95;
$color-red: #C94800;
$color-skyblue: #DDF0F1;
$color-gray: #9A9A9A;

body {
  color: $color-black;
}